import { Form, Button, Container } from "react-bootstrap";
import ValidateSimpleRegistration from "../../Helpers/ValidateSimpleRegistration";
import { toast } from "react-toastify";
const notify = (message) => toast(message);

function SimpleRegistrationComp({ text, btn, clickHandler }) {
  return (
    <Container className="mt-3 col-6">
      <Form noValidate>
        <h1>{text}</h1>
        <h5>You can open new account for free!</h5>

        <Form.Group className="mb-3" controlId="formBasicFirstName">
          <Form.Label>First Name</Form.Label>
          <Form.Control
            type="name"
            placeholder="First name"
            // defaultValue="rosh"
          />
        </Form.Group>

        <Form.Group className="mb-3" controlId="formBasicLastName">
          <Form.Label>Last Name</Form.Label>
          <Form.Control
            type="name"
            placeholder="Last name"
            // defaultValue="rosh"
          />
        </Form.Group>
        <Form.Group className="mb-3" controlId="formBasicEmail">
          <Form.Label>Email</Form.Label>
          <Form.Control
            type="email"
            placeholder="Enter Email"
            // defaultValue="rosh1@gmail.com"
          />
        </Form.Group>

        <Form.Group className="mb-3" controlId="formBasicPassword">
          <Form.Label>Password</Form.Label>
          <Form.Control
            type="password"
            placeholder="Create Password"
            // defaultValue="123456"
          />
        </Form.Group>

        <Button
          variant="dark"
          type="submit"
          onClick={(e) => {
            e.preventDefault();
            var validatioErrorOrData = ValidateSimpleRegistration(
              "formBasicEmail",
              "formBasicPassword",
              "formBasicFirstName",
              "formBasicLastName"
            );
            if (typeof validatioErrorOrData == "string") {
              notify(validatioErrorOrData);
            } else {
              clickHandler(validatioErrorOrData);
              window.location.href = "http://localhost:3001/sign-in";
            }
          }}
        >
          {btn}
        </Button>
      </Form>
    </Container>
  );
}
export default SimpleRegistrationComp;
