import { toast } from "react-toastify";
import { Card, Button } from "react-bootstrap";
import { RiDeleteBin6Line } from "react-icons/ri";

function CardComp({ card, handleClick }) {
  return (
    (card && (
      <Card
        style={{
          width: "18rem",
          display: "inline-block",
          paddingLeft: 20,
          paddingTop: 10,
          margin: 10,
          marginTop: 20,
        }}
      >
        <Card.Img variant="top" style={{ width: 80 }} src={card.bizImage} />
        <Card.Body>
          <Card.Title style={{ fontWeight: "bold" }}>{card.bizName}</Card.Title>
          <Card.Text>{card.bizDescription}</Card.Text>
          <Card.Text>{card.bizAddress}</Card.Text>
          <Card.Text>{card.bizPhone}</Card.Text>
          <Button
            style={{
              fontSize: 22,
              color: "white",
              backgroundColor: "none",
              padding: 4,
            }}
            variant="link"
            onClick={(e) => {
              e.preventDefault();
              toast("The card has been removed!");
              handleClick(card._id);
            }}
          >
            <RiDeleteBin6Line class="removeCardIcon"></RiDeleteBin6Line>
          </Button>
        </Card.Body>
      </Card>
    )) || <></>
  );
}
export default CardComp;
